@import 'reset';
@import 'mixin';
@import 'grid';

$backgroundColor: #e1e1e1;
$mainColor: #4A90E2;
$hoverColor: #00408B;
$remarkBackgroundColor: #F2F2F2;
$stateColor: #757575;
$starColor: #F5A623;
$importantBackground: #FFF2DC;
$addedBackground: #C8C8C8;
$buttonColor: #D0021B;

body {
    font-size: 16px;
    font-family: "Roboto","PingFang TC","Noto Sans TC";
    background-color: $backgroundColor;
    .no-gutters {}
    margin-right: 0;
    margin-left: 0;
    > .col,> [class*="col-"] {
        padding-right: 0;
        padding-left: 0; }
    .wrap {
        max-width: 100%;
        margin: 0 auto;
        .header {
            width: 100%;
            background-color: $mainColor;
            height: 76px;
            li {
                text-align: center;
                height: 76px;
                line-height: 76px;
                border-bottom: 5px solid $mainColor;
                width: 160px;
                margin: 0 30px;
                &.active {
                    border-bottom: 5px solid $hoverColor;
                    a {
                        color: white; } }
                &:hover {
                    border-bottom: 5px solid $hoverColor; }
                a {
                    font-size: 24px;
                    color: $hoverColor;
                    text-decoration: none; } } }
        .addRemark {
            width: 620px;
            height: 65px;
            background-color: #fff;
            border: 2px solid #C8C8C8;
            border-radius: 5px;
            margin: 24px auto;
            cursor: pointer;
            .text {
                font-size: 24px;
                color: #C8C8C8;
                line-height: 60px;
                .addIcon {
                    font-size: 26px;
                    margin: 0px 1px 0px 25px; } } }
        .Switching {
            visibility: hidden;
            height: 0; }
        .addedMode {
            display: none; }
        .added {
            width: 620px;
            height: 520px;
            margin: 24px auto;
            background: #F2F2F2;
            box-shadow: 0 4px 4px 0 $addedBackground;
            border-radius: 5px;
            position: relative;
            display: none;
            .active {
                background-color: $importantBackground;
                .titleInput {
                    background-color: $importantBackground !important; } }
            .addTitle {
                height: 76px;
                border-bottom: 2px solid #C8C8C8;
                .titleInput {
                    border: none;
                    background-color: $remarkBackgroundColor;
                    margin: 24px 0 0 72px;
                    width: 250px;
                    height: 28px; }
                input[type=checkbox] {
                    transform: scale(1.7);
                    position: absolute;
                    left: 32px;
                    top: 32px; }
                input::-webkit-input-placeholder {
                    font-size: 24px;
                    color: #000;
                    font-family: "Roboto"; }
                i {
                    position: absolute;
                    top: 24px;
                    font-size: 24px;
                    cursor: pointer; }
                .fa-star-o,.fa-star {
                    right: 88px; }
                .fa-star {
                    color: #F5A623; }
                .fa-pencil {
                    right: 32px; }
                .inEdit {
                    color: $mainColor; } }
            .addContent {
                position: relative;
                .dateBlock, .fileBlock, .comment {
                    position: relative; }
                i {
                    position: absolute;
                    font-size: 20px;
                    cursor: pointer; }
                .fa-calendar,.fa-file-o,.fa-commenting-o {
                    top: 27px;
                    left: 72px; }
                span {
                    display: block;
                    padding: 27px 0 0 96px;
                    font-size: 20px;
                    color: #000000; }
                .year,.hour {
                    border: none;
                    text-align: center;
                    transform: scale(1.5);
                    font-size: 10px;
                    &::-webkit-input-placeholder {
                        font-family: Roboto-Regular;
                        font-size: 16px;
                        color: $addedBackground;
                        padding-left: 16px; } }
                .year {
                    margin: 14px 0 0 129px; }
                .hour {
                    margin-left: 75px; }
                .upload {
                    width: 32px;
                    height: 32px;
                    line-height: 32px;
                    text-decoration: none;
                    text-align: center;
                    display: block;
                    background: #C8C8C8;
                    border-radius: 2px;
                    margin: 11px 0 0 96px;
                    font-size: 22px;
                    font-weight: bolder;
                    color: white;
                    transition: all .3s;
                    &:hover {
                        background-color: $mainColor;
                        color: white; }
                    input[type=file] {
                        position: absolute;
                        width: 32px;
                        height: 32px;
                        top: 58px;
                        left: 96px;
                        opacity: 0;
                        overflow: hidden; } }
                .fileName {
                    position: absolute;
                    top: 65px;
                    left: 146px;
                    font-size: 16px;
                    padding: 0px; }
                textarea {
                    width: 455px;
                    height: 120px;
                    border: none;
                    margin: 8px 0 0 96px;
                    padding: 10px;
                    font-size: 16px;
                    color: $addedBackground;
                    line-height: 24px;
                    border-radius: 2px; } }
            .addButton {
                position: absolute;
                bottom: 0;
                height: 60px;
                a {
                    display: inline-block;
                    float: left;
                    width: 310px;
                    height: 60px;
                    line-height: 60px;
                    text-align: center;
                    background-color: #fff;
                    font-size: 24px;
                    color: #D0021B;
                    text-decoration: none;
                    transition: all .3s;
                    span {
                        font-size: 30px;
                        padding-right: 15px; } }
                .addBtn {
                    color: #fff;
                    background-color: $mainColor; } } }
        .remark {
            width: 620px;
            margin: 0 auto;
            li {
                border-radius: 5px;
                background-color: $remarkBackgroundColor;
                display: block;
                margin-bottom: 8px;
                position: relative; }
            .matchLi {
                display: none; }
            .active {
                background-color: $importantBackground;
                border: 1px solid $importantBackground;
                .modifyTitle {
                    background-color: $importantBackground !important; } }
            .titleAndState {
                input[type=checkbox] {
                    transform: scale(1.7);
                    position: absolute;
                    left: 32px;
                    top: 32px; }
                input:checked ~ .remarkTitle {
                    text-decoration: line-through; }
                .modifyTitle {
                    border: none;
                    background-color: #F2F2F2;
                    margin: 24px 0px 24px 72px;
                    width: 250px;
                    height: 28px;
                    display: none;
                    &::-webkit-input-placeholder {
                        font-size: 24px;
                        color: #000;
                        font-family: "Roboto"; } }
                .remarkTitle {
                    display: inline-block;
                    font-size: 24px;
                    color: #000000;
                    margin: 24px 0px 24px 72px;
                    width: 262px;
                    height: 26px;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap; }
                i {
                    position: absolute;
                    top: 24px;
                    font-size: 24px;
                    cursor: pointer; }
                .fa-star-o,.fa-star {
                    right: 144px; }
                .fa-star {
                    color: $starColor; }
                .fa-pencil {
                    right: 88px; }
                .inEdit {
                    color: $mainColor; }
                .fa-trash-o {
                    right: 32px; }
                .titleEditMode {
                    display: inline-block; }
                .spanDisapper {
                    display: none; } }
            .titleEditState {
                border-bottom: 2px solid $addedBackground; }
            .remarkState {
                i {
                    font-size: 18px;
                    position: static;
                    cursor: auto;
                    &:nth-child(1) {
                        margin: 0px 0px 17px 72px; }
                    &:nth-child(2) {
                        margin: 0 16px; }
                    &>span {
                        padding-left: 8px; } }
                span {
                    font-size: 16px;
                    color: $stateColor; } }
            .editDisapper {
                display: none; }
            .editIng {
                height: 445px;
                width: 620px;
                position: relative;
                display: none;
                .dateBlock, .fileBlock, .comment {
                    position: relative; }
                i {
                    position: absolute;
                    font-size: 20px;
                    cursor: pointer; }
                .fa-calendar,.fa-file-o,.fa-commenting-o {
                    top: 27px;
                    left: 72px; }
                span {
                    display: block;
                    padding: 27px 0 0 96px;
                    font-size: 20px;
                    color: #000000; }
                .year,.hour {
                    border: none;
                    text-align: center;
                    transform: scale(1.5);
                    font-size: 10px;
                    &::-webkit-input-placeholder {
                        font-family: Roboto-Regular;
                        font-size: 16px;
                        color: $addedBackground;
                        padding-left: 16px; } }
                .year {
                    margin: 14px 0 0 129px; }
                .hour {
                    margin-left: 75px; }
                .upload {
                    width: 32px;
                    height: 32px;
                    line-height: 32px;
                    text-decoration: none;
                    text-align: center;
                    display: block;
                    background: #C8C8C8;
                    border-radius: 2px;
                    margin: 11px 0 0 96px;
                    font-size: 22px;
                    font-weight: bolder;
                    color: white;
                    transition: all .3s;
                    &:hover {
                        background-color: $mainColor;
                        color: white; }
                    input[type=file] {
                        position: absolute;
                        width: 32px;
                        height: 32px;
                        top: 58px;
                        left: 96px;
                        opacity: 0;
                        overflow: hidden; } }
                .fileName {
                    position: absolute;
                    top: 65px;
                    left: 146px;
                    font-size: 16px;
                    padding: 0px; }
                textarea {
                    width: 455px;
                    height: 120px;
                    border: none;
                    margin: 8px 0 0 96px;
                    padding: 10px;
                    font-size: 16px;
                    color: $addedBackground;
                    line-height: 24px;
                    border-radius: 2px; }
                .addButton {
                    position: absolute;
                    bottom: 0;
                    height: 60px;
                    a {
                        display: inline-block;
                        float: left;
                        width: 310px;
                        height: 60px;
                        line-height: 60px;
                        text-align: center;
                        background-color: #fff;
                        font-size: 24px;
                        color: #D0021B;
                        text-decoration: none;
                        transition: all .3s;
                        span {
                            padding: 0px;
                            display: inline;
                            font-size: 30px;
                            padding-right: 15px;
                            color: unset; } }
                    .saveBtn {
                        color: #fff;
                        background-color: $mainColor; } } }
            .editMode {
                display: block; } }
        .remain {
            font-family: Roboto-Italic;
            font-size: 24px;
            color: #C8C8C8;
            margin: 0 auto;
            width: 620px;
            padding-left: 30px;
            display: none; }
        .done {
            display: none; }
        .active {
            display: block; } } }
